<template>
  <div v-show="visible" class="mint-sub">
    <mt-popup ref="subPopup" v-model="visible" position="right" :modal="false" class="mint-data-sub overflow-auto">
      <mt-header class="stitle">
        <label class="header-title" slot="left">选择附加险</label>
        <i class="iconfont icon-guanbi" slot="right" @click="close"></i>
      </mt-header>
      <!--<mt-checklist class="stitle checklist-title-none" v-model="check[1063]" :options="[{label:'附加健康源2号增强版终身重大疾病保险',value:'1',disabled:true}]">
      </mt-checklist>
      <div v-show="check[1063][0] === '1'">
        <mt-cell title="交费方式" v-model="subCal[1063].option.payType.val_text"></mt-cell>
        <mt-cell title="交费年期" v-model="subCal[1063].option.payTerm.val_text"></mt-cell>
        <mt-cell title="保险期间" v-model="subCal[1063].option.insTerm.val_text"></mt-cell>
        <mt-cell title="主险祝寿金领取年龄" v-model="subCal[1063].option.getAge.val_text"></mt-cell>
        <mt-cell title="保额(元)" v-model="subCal[1063].option.amount.val"></mt-cell>
        <mt-cell title="保费(元)" v-model="subCal[1063].option.prem.val"></mt-cell>
      </div>-->
      <!--<mt-checklist v-model="check[1064]" :value="remitted('1064.option.remitted.val')" :options="[{label:'附加投保人豁免保险费C款重大疾病保险',value:'1'}]">
      </mt-checklist>
      <div v-if="check[1064][0] === '1'">
        <mt-cell title="投保人出生日期" v-model="subCal[1064].option.holderBirthday.val" v-verify.sub="'required'" data-verify="{error:['请选择投保人出生日期'],blur:false}">
          <span class="mint-cell-value is-link" @click="openDataPicker('holderDatePicker')">{{subCal[1064].option.holderBirthday.val_text}}
            <i class="mint-cell-allow-right"></i>
          </span>
        </mt-cell>

        <mt-cell title="投保人性别" v-model="subCal[1064].option.holderSex.val" v-verify.sub="'required'" data-verify="{error:['请选择投保人人性别'],blur:false}">
          <span class="mint-cell-value is-link" @click="openDataPicker('holderPicker')">{{subCal[1064].option.holderSex.val_text}}
            <i class="mint-cell-allow-right"></i>
          </span>
        </mt-cell>

        <mt-cell title="与主被保人关系" v-model="subCal[1064].option.relation.val" v-verify.sub="'required'" data-verify="{error:['请选择与主被保人关系'],blur:false}">
          <span class="mint-cell-value is-link" @click="openDataPicker('relationPicker')">{{subCal[1064].option.relation.val_text}}
            <i class="mint-cell-allow-right"></i>
          </span>
        </mt-cell>

        <mt-cell title="交费方式" v-model="subCal[1064].option.payType.val_text"></mt-cell>

        <mt-cell title="交费年期" v-model="subCal[1064].option.payTerm.val" v-verify.sub="'required'" data-verify="{error:['请选择附加豁免交费年期'],blur:false}">
          <span class="mint-cell-value is-link" @click="openDataPicker('payTermPicker')">{{subCal[1064].option.payTerm.val_text}}
            <i class="mint-cell-allow-right"></i>
          </span>
        </mt-cell>
        <mt-cell title="保险期间" v-model="subCal[1064].option.insTerm.val_text"></mt-cell>
        <mt-cell title="保额(元)" v-model="calcExemptionAmount"></mt-cell>
        <mt-cell title="保费(元)" v-model="subCal[1064].option.prem.val"></mt-cell>

        <datetime-picker ref="holderDatePicker" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日"
          v-model="birthDay" :start-date="limitDate.startDate" :end-date="limitDate.endDate" @confirm="onHolderDateConfirm"
          @cancel="onPopupCancel">
        </datetime-picker>
        <data-picker ref="holderPicker" :slots="options.sex" :value-key="'label'" :default="subCal[1064].option.holderSex" @confirm="onDataConfirm(arguments[0], arguments[1], 'subCal[1064].option.holderSex')"
          @cancel="onPopupCancel('subCal[1064].option.holderSex.val')"></data-picker>
        <data-picker ref="relationPicker" :slots="options.relationSlots" :value-key="'label'" :default="subCal[1064].option.relation"
          @confirm="onDataConfirm(arguments[0], arguments[1], 'subCal[1064].option.relation')" @cancel="onPopupCancel('subCal[1064].option.relation.val')"></data-picker>
        <data-picker ref="payTermPicker" :slots="options.subPayTermSlots" :value-key="'label'" :default="subCal[1064].option.payTerm"
          @confirm="onDataConfirm(arguments[0], arguments[1], 'subCal[1064].option.payTerm')" @cancel="onPopupCancel('subCal[1064].option.payTerm.val')"></data-picker>

      </div>
      <mt-checklist v-model="check[966]" :options="[{label:'附加住院费用医疗保险',value:'1'}]">
      </mt-checklist>
      <div v-if="check[966][0] === '1'">
        <mt-cell title="交费方式" v-model="subCal[966].option.payType.val_text"></mt-cell>
        <mt-cell title="交费年期" v-model="subCal[966].option.payTerm.val_text"></mt-cell>
        <mt-cell title="保险期间" v-model="subCal[966].option.insTerm.val_text"></mt-cell>
        <mt-field label="保额(元)" ref="field" v-model.number="subCal[966].option.amount.val" v-verify.sub="'required|positiveInt'"
          data-verify="{error:['请输入附加住院医疗保险保额','请正确输入附加住院医疗保险保额']}" placeholder="请输入保额" type="number"></mt-field>
        <mt-cell title="保费(元)" v-model="subCal[966].option.prem.val"></mt-cell>
      </div>-->
     	<mt-checklist v-model="check[1088]" :options="[{label:'天安人寿附加金如意C款年金保险（万能型）',value:'1'}]">
      </mt-checklist>
      <div v-if="check[1088][0] === '1'">
        <mt-cell title="交费方式" v-model="subCal[1088].option.payType.val_text"></mt-cell>
        <mt-cell title="保险期间" v-model="subCal[1088].option.insTerm.val_text"></mt-cell>
        <!--<mt-cell title="保费(元)" v-model="subCal[1088].option.prem.val"></mt-cell>-->
        <mt-field label="保费(元)" ref="field" v-model.number="subCal[1088].option.prem.val" v-verify.sub="'required|integerNum'" data-verify="{error:['请输入保费','请正确输入保费']}" placeholder="请输入保费" type="number"></mt-field>
      </div>
      <mt-checklist v-model="check[966]" :options="[{label:'附加住院费用医疗保险',value:'1'}]">
      </mt-checklist>
      <div v-if="check[966][0] === '1'">
        <mt-cell title="交费方式" v-model="subCal[966].option.payType.val_text"></mt-cell>
        <mt-cell title="交费年期" v-model="subCal[966].option.payTerm.val_text"></mt-cell>
        <mt-cell title="保险期间" v-model="subCal[966].option.insTerm.val_text"></mt-cell>
        <mt-field label="保额(元)" ref="field" v-model.number="subCal[966].option.amount.val" v-verify.sub="'required|positiveInt'"data-verify="{error:['请输入附加住院医疗保险保额','请正确输入附加住院医疗保险保额']}" placeholder="请输入保额" type="number"></mt-field>
        <mt-cell title="保费(元)" v-model="subCal[966].option.prem.val"></mt-cell>
      </div>
      <mt-checklist v-model="check[968]" :options="[{label:'附加住院津贴保险',value:'1'}]">
      </mt-checklist>
      <div v-if="check[968][0] === '1'">
        <mt-cell title="交费方式" v-model="subCal[968].option.payType.val_text"></mt-cell>
        <mt-cell title="交费年期" v-model="subCal[968].option.payTerm.val_text"></mt-cell>
        <mt-cell title="保险期间" v-model="subCal[968].option.insTerm.val_text"></mt-cell>
        <mt-field label="份数" ref="field" v-model.number="subCal[968].option.share.val" v-verify.sub="'required|positiveInt'"
          data-verify="{error:['请输入份数','请正确输入份数']}" placeholder="请输入份数" type="number"></mt-field>
        <mt-field label="保额(元)" ref="field" v-model.number="subCal[968].option.amount.val" v-verify.sub="'required|positiveInt'"
          data-verify="{error:['请输入附加意外伤害保险保额','请正确输入附加意外伤害保险保额']}" placeholder="请输入保额" type="number"></mt-field>
        <mt-cell title="保费(元)" v-model="subCal[968].option.prem.val"></mt-cell>
      </div>
    </mt-popup>
  </div>
</template>


<script type="text/babel">
import dataPicker from '@/pages/common/data-picker'
import dateTimePicker from '@/pages/common/datetime-picker'
export default {
  name: 'mt-data-sub',
  props: {
    subCal: {
      type: Object,
      default: {}
    },
    calRisk: {
      type: Object,
      default: {}
    },
    options: {
      type: Object,
      default: {}
    },
    params: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      visible: false,
      birthDay: '',
      check: {},
    //   renewFlag: {} //是否续保
    };
  },
  created() {
    this.initData();
  },
  watch: {
    visible(val, oldVal) {
      if (val === false) {
        this.$emit('cancel');
      }
    },
    check: {
      handler(val, oldVal) {
        for (let flag in val) {
          this.subCal[flag].option.flag.val = val[flag][0] === '1' ? '1' : 0; //为 0 时，产品引擎仅能识别数字
        }
        this.$nextTick(() => {
          if (this.$refs.field) {
            let that = this;
            this.$utils.koala(this.$refs.field, {
              delay: 500,
              keyup: function (event) {
                that.calComp();
              }
            })
          }
        });
        this.calComp();
      },
      deep: true
    },
    // renewFlag: {
    //   handler(val, oldVal) {
    //     for (let flag in val) {
    //       this.subCal[flag].option.renewFlag.val = val[flag][0] === '1' ? '1' : 0;
    //     }
    //     this.calComp();
    //   },
    //   deep: true
    // }
  },
computed: {
    limitDate() {
      let now = new Date();
      /**计算天数 */
      let calDate = function (date, days) {
        date.setDate(date.getDate() + days);
        return date;
      }
      return {
        startDate: new Date(this.$utils.calYear(calDate(now, 2), -61)),
        endDate: calDate(now, -29)
      };
    },
    /**计算豁免保额 */
    calcExemptionAmount() {
      return (Number(this.subCal[1063].option.prem.val || '0') + Number(this.calRisk.mainOption.prem.val || '0')) * Number(this.subCal[1064].option.payTerm.val || '0');
    },
    /**
     * 被保人性别
     */
    insureSex() {
      return this.calRisk.basicOption.sex.val;
    },
    /**
     * 被保人年龄
     */
    insureAge() {
      return Number(this.$utils.calcAge(this.calRisk.basicOption.age.val || '0'));
    },
    /**
     * 投保人性别
     */
    holderSex() {
      return this.subCal[1064].option.holderSex.val;
    },
    /**
     * 投保人年龄
     */
    holderAge() {
      return Number(this.$utils.calcAge(this.subCal[1064].option.holderBirthday.val || '0'));
    },
    /**
     * 获取投被保关系
     */
    insuredRelation() {
      return this.subCal[1064].option.relation.val;
    },
    /**
     * 是否投了豁免险
     */
    hasRemitted() {
      return this.check[1064][0] === '1';
    }
},
  methods: {
    open() {
      this.visible = true;
    },
    close() {
      this.$utils.modalHelper.beforeClose();//解决弹层滚动穿透
      this.visible = false;
      this.$emit('cancel');
      document.setTitle('产品试算');
    },
    confirm() {
      this.visible = false;
      this.$emit('confirm', this.$refs.subPopup);
    },
    /**
     * 初始化数据
     */
    initData() {
      this.birthDay = this.subCal[1064].option.holderBirthday.val || '';
      /**处理附加险选中 */
      let check = {}
        //   renewFlag = {};
      this.$utils.forEach(this.subCal, (subOp, key) => {
        subOp.mainRiskOutCode = this.params.riskCode;
        check[key] = subOp.option.flag.val === '1' ? ['1'] : [];
        // if (subOp.option.renewFlag) {
        //   renewFlag[key] = subOp.option.renewFlag.val === '1' ? ['1'] : [];
        // }
      });
      this.check = check;
    //   this.renewFlag = renewFlag;
    },
    /**
     * 校验附加险数据
     */
    validate(validOnly) {
      if (this.$verify.check('sub', validOnly)) {
          if (this.hasRemitted) { //投了豁免险，校验投保人信息
              if (this.insuredRelation === '01') {
                  this.$toast('附加豁免重疾时，与主被保人关系不能是本人。');
                  return false;
              }
              if (this.insuredRelation === '02' && (this.holderSex === '0' && this.holderAge < 22 || this.holderSex === '1' && this.holderAge < 20 || this.insureSex === '0' && this.insureAge < 22 || this.insureSex === '1' && this.insureAge < 20)) {
                  this.$toast('投被保人为夫妻时，年龄不符合要求');
                  return false;
              }
              if (this.$utils.inArray(this.insuredRelation, ['03', '24', '25', '26', '27']) !== -1) {//父母、爷爷、奶奶、外公、外婆
                  if (this.insureAge < this.holderAge) {
                      this.$toast('被保险人年龄不应小于投保人年龄');
                      return false;
                  }
              }
              if (this.$utils.inArray(this.insuredRelation, ['04', '34', '28']) !== -1) {//儿女、孙子/女、外孙子/女
                  if (this.insureAge > this.holderAge) {
                      this.$toast('被保险人年龄不应大于投保人年龄');
                      return false;
                  }
              }
          }
          return true;
      }
      return false;
    },
    /**
     * 弹出数据选择框
     */
    openDataPicker(picker) {
      this.$refs.subPopup.$el.classList.remove('overflow-auto');
      this.$refs[picker].open()
    },
    /**
     * 弹窗回调
     */
    onDataConfirm(picker, values, model) {
      this.$utils.setModelValue(this.$utils.get(this, model), values[0]);
      this.$nextTick(() => {
        this.onPopupCancel(`${model}.val`);
        this.calComp();
      });
    },
    /**
     * 弹窗取消校验数据
     */
    onPopupCancel(model) {
      this.$verify.validate(model);
      setTimeout(() => {
        this.$refs.subPopup.$el.classList.add('overflow-auto');
      }, 100);
    },
    /**
     * 投保人出生日期回调
     */
    onHolderDateConfirm(value) {
      this.$utils.setModelValue(this.subCal[1064].option.holderBirthday, this.$utils.formatDateTime(value));
      this.$nextTick(() => {
        this.onPopupCancel('subCal[1064].option.holderBirthday.val');
        this.calComp();
      });
    },
    /**
     * 触发试算
     */
    calComp() {
      this.visible && this.$emit('calComp');
    },
    /**
     * 处理试算联动输出
     */
 updateDataPicker(code, opt) {
   if (this.subCal[code] && Array.isArray(opt.val) && opt.val.length > 0) {
     let cameCode = this.$utils.camelCase(opt.code.substring(5));
     let subOption = this.subCal[code].option[cameCode];
     subOption.opt = opt.val;
     subOption && (((this.$utils.get(this.options, `sub${cameCode.substring(0,1).toUpperCase()}${cameCode.substring(1)}Slots`) || [])[0] || {}).values || []).forEach(slotVal => {
       if (slotVal.value === String(opt.val[0])) {
         subOption.val_text = slotVal.label;
         subOption.val = slotVal.value;
       }
     });
   }
 },
    /**豁免标识 */
    remitted(path) {
      this.$utils.attribute(this.subCal, path, true, (key, Obj) => {
        if (key === 'remitted') { //豁免标识
          Obj.val = '1';
        }
      });
    }
  },
  components: {
    'datetime-picker': dateTimePicker,
    dataPicker
  }
};
</script>
<style lang="scss">
.mint-sub {
  .mint-header {
    color: #000;
    height: 50px;
    line-height: 50px;
    .header-title {
      font-size: 16px;
      line-height: 2;
    }
    i {
      font-size: 20px;
      margin-right: 10px;
    }
  }
  .stitle {
    background-color: #F7F7F7;
    .mint-checklist-title {
      font-size: 16px;
      color: #000;
      padding-top: 7px;
      line-height: 2;
    }
  }
  .stitle.checklist-title-none {
    .mint-checklist-title {
      display: none;
    }
  }
  .mint-data-sub {
    width: 100%;
    height: 100%;
    // margin-top: 45px;
    pointer-events: auto;
    .close {
      position: absolute;
      right: 20px;
      top: 20px;
      i {
        font-size: 20px;
      }
    }
    .mint-field-core {
      text-align: right;
    }
    .mint-cell-title,
    .mint-cell-value {
      font-size: 93%;
    }
  }
  .overflow-auto {
    overflow: auto;
  }
  .mint-sub-action {
    width: 100%;
	
    text-align: right;
    position: fixed;
    top: 0;
	
    padding: 10px 0 10px 0;
    background-color: white;
    pointer-events: auto;
    span {
      border-radius: 100%;
      vertical-align: middle;
      margin-right: 15px;
      background-color: #ccc;
      padding: 4.5px 10px;
      color: #000;
    }
  }
  .checklist-right {
    .mint-checklist-label {
      padding-left: 0;
      padding-right: 0;
    }
    .mint-checkbox-label {
      color: #9C9A9C;
      margin-left: 0;
    }
  }
}

</style>


